	import Helper from "@/function.js"
	import Md5Unit from '@/pages/utils/md5.js'	
export default {
	data() {
		return {
			parentId: "",
			userPhone:"",
			userPass:"",
			checkCode:"",
			userPass_2:"",
			checkcode_btn:"获取验证码",
			timecount: 60,
			license:false,
		};
	},
	
	onLoad:function(options){		
		uni.removeStorageSync("token")
	},	
	methods:{
		
		GetCheckCode: function() {
			var _this = this;
		
			if (Helper.isEmpty(this.userPhone) || _this.userPhone.length != 11) {
				uni.showModal({
					title: "信息提示",
					content: "请输入正确的手机号",
					showCancel: false
				})
				_this.checkcode_btn = '获取验证码'
				_this.timecount = 60;
				return;
			}
			
			uni.showLoading({
				title:"请等待..."
			})
		
			
			uni.request({
				url: _this.ApiGateWay + "/sale/message/GetCheckCode?type=1&phone=" + _this.userPhone,
				method: "POST",
				success: function(res) {
					uni.hideLoading();
					if (Helper.CheckData(res)) {
						_this.GetCode();
					} else {
						uni.showModal({
							title: "信息提示",
							content: res.data.errorMessage,
							showCancel: false
						})
					}
		
				},
				fail: function() {
					uni.hideLoading();
					uni.showModal({
						title: "信息提示",
						content: "网络错误",
						showCancel: false
					})
				}
		
		
			});
		},
		
		GetCode: function() {
			var _this = this;
		
			if (this.timecount == 0) {
				this.checkcode_btn = '获取验证码'
				this.timecount = 60;
			} else {
				_this.checkcode_btn = _this.timecount + '秒后重发'
				_this.timecount--;
				setTimeout(function() {
					_this.GetCode();
		
				}, 1000)
			}
		
		
		},
	
		goReg(){
			var _this = this;
			if (Helper.isEmpty(this.userPhone) || this.userPhone.length !=11)
			{
				uni.showToast({
					title:"请输入正确的手机号",
					icon:"none"
				})
				
				return;
			}
			
			if (Helper.isEmpty(this.checkCode) || this.checkCode.length != 6)
			{
				uni.showToast({
					title:"请输入6位验证码",
					icon:"none"
				})
				
				return;
			}
			
			if (Helper.isEmpty(this.userPass) || this.userPass.length < 6)
			{
				uni.showToast({
					title:"请输入6位或以上新密码",
					icon:"none"
				})
				
				return;
			}
			
			if (this.userPass != this.userPass_2)
			{
				uni.showToast({
					title:"两次密码输入不一致",
					icon:"none"
				})
				
				return;
			}
			
			
			
			
			var md5Pass = Md5Unit.hex_md5(this.userPass);;
			
			var params = {
				userPhone: _this.userPhone,
				userPass: md5Pass,
				checkCode: _this.checkCode,
				appId: _this.appId,
				parentId:_this.parentId
			}
			
			uni.request({
				url: _this.ApiGateWay + "/sale/Member/FindPass",
				data: params,
				dataType:"json",
				method:"POST",
				success:function(res){
					if (Helper.CheckData(res))
					{
						uni.showModal({
							title:"信息提示",
							content:"密码重置成功,去登录",
							showCancel:false,
							success:function(res){
								if (res.confirm)
								{
									uni.reLaunch({
										url:"/pages/sign/sign"
									})
								}
							}
						})
					}
					else
					{
						uni.showModal({
							title:"出错了",
							content:res.data.errorMessage,
							showCancel:false
						})
					}
				}
				
			})
			
		},
		
		
	}
};
